// style/sass/contextmenu.sass
#contextmenu {
  position: fixed;
  z-index: 1000;
  background-color: #fff;
  width: auto;
  min-width: 200px;
  font-family: sans-serif;
  font-size: 11px;
  padding: 2px 0px 2px 0px;
  height: auto;

  ul {
    margin: 0px;
    width: 100%;
    padding: 0px;

    & + ul {
      border-top: 1px solid #a1a1a1; }

    li {
      list-style: none;
      padding: 4px 12px 4px 12px;
      width: calc(100% - 24px);

      &:hover {
        background-color: #c1c1c1;
        cursor: pointer; }
      p {
        margin: 0px; } } } }
